import React, {Component} from 'react';
import ListCreater from "./js/list_creater";

class Main extends Component {
    render() {
        const tpop_pad={padding:"1em",marginTop:"2em"},
        lpop_pad={fontFamily:"fantasy",letterSpacing:"3px"};
        let lobbylist;
        if(this.props.lobbylist==="Loading"){
            lobbylist="Loading";
        }else{
            lobbylist=this.props.lobbylist.map((lobbylist) => (<ListCreater lobbyid={lobbylist.lobbyid} lobbyname={lobbylist.lobbyname} joined={lobbylist.joined} />));
        }
        return (
            <div className="main">
                <div className="col-md-10 col-sm-12" style={tpop_pad}>
                    <button className="btn btn-danger float-right" id="log_me_out" onClick={this.props.LogmeOut}>Log Out</button>
                    <form method="post" className="d-none" id="login_out_form">
                        <input type="text" id="user_token" value={this.props.user_token} name="token"/>
                    </form>
                    <br/><br/><br/><br/>
                        <h3 style={lpop_pad}>Lobby List:</h3>
                        <br/>
                            <table className="table">
                                <thead>
                                <tr>
                                    <th scope="col">ID</th>
                                    <th scope="col">Lobby Name</th>
                                    <th scope="col">People Joined</th>
                                    <th scope="col">Action</th>
                                </tr>
                                </thead>
                                <tbody id="lobby_list_all">
                                    {lobbylist}
                                </tbody>
                            </table>
                </div>
            </div>
        );
    }
}

export default Main;