import React, {Component} from 'react';

class SideNav extends Component {
    render() {
        return (
            <div className="sidenav">
                <h2 id="share_interest_head">Shareinterest<sub id="madeby_name">Made
                    by Aniket Mahajan ( FYMCA32 )</sub></h2>
                <div className="login-main-text">
                    <h2>{this.props.welcomehead}</h2>
                    <p id="login_intro">{this.props.welcomepara}</p>
                </div>
            </div>
        );
    }
}

export default SideNav;