import React, {Component} from 'react';
import './App.css';
import {BrowserRouter as Router, Switch, Redirect , Route} from 'react-router-dom';
import Home from "./Home";
import Lobbylist from "../LobbyList/lobbylist";
import getCookie from "../../Module/getCookie";
import LobbyView from "../LobbyView/LobbyView";

class App extends Component {
    render() {
        const packing=<Switch>
            <Route path="/shareinterest/build/LobbyView/:lobbyid/:lobbyname" exact component={LobbyView} />
            <Route path="/shareinterest/build/lobbylist" exact component={Lobbylist} />
            <Route path="/shareinterest/build/" exact component={Home} />
            <Route path="/shareinterest/build/index.html" exact component={Home} />
            <Route path="/shareinterest/build/index" exact component={Home} />
        </Switch>;
        if(getCookie("token")===null || getCookie("token") === ""){
            document.cookie = "token= ; path=/; max-age=" + 7*24*60*60;
            return (
                <Router>
                    {packing}
                </Router>
            );
        }else if(getCookie("token")!=="undefined"){
            return (
                <Router>
                    {packing}
                    <Redirect to="/shareinterest/build/lobbylist" />
                </Router>
            );
        }
    }
}

export default App;