import React, {Component} from 'react';

class MsgBody extends Component {
    constructor(props) {
        super(props);
    }
    render() {
        if(this.props.user_token===this.props.msg_token){
            return (
                <li className="in">
                    <div className="chat-body">
                        <div className="chat-message">
                            <h5>{this.props.username}</h5>
                            <p>{this.props.msg}</p>
                        </div>
                    </div>
                </li>
            );
        }else{
            return (
                <li className="out">
                    <div className="chat-body">
                        <div className="chat-message">
                            <h5>{this.props.username}</h5>
                            <p>{this.props.msg}</p>
                        </div>
                    </div>
                </li>
            );
        }
    }
}

export default MsgBody;