import React, {Component} from 'react';
import SideNav from "../App/SideNav";
import Main from "./Main";
import getCookie from "../../Module/getCookie";
import {Redirect} from "react-router-dom";

class Lobbylist extends Component {
    constructor(props) {
        super(props);
        this.state= {
          redirect_to:0,
          token:"",
          user_name:"loading",
          lobbylist:"Loading",
          lobbyid:"",
          lobbyname:""
        };
        this.log_me_out = () =>{
            if(this.state.user_token==null){
                this.setState({
                    redirect_to:1
                });
                return false;
            }
            const form=new FormData(document.getElementById("login_out_form"));
            const real_data=new URLSearchParams();
            for(const pair of form){
                real_data.append(pair[0],pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/log_out.php", {
                headers: { "Content-Type": "application/x-www-form-urlencoded" },
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    if(response.status==="success"){
                        document.cookie = "token= ; path=/; max-age=" + 0;
                        this.setState({
                            redirect_to:1
                        });
                        return false;
                    }else{
                        alert('Unknown error occurred, please try again later');
                        this.setState({
                            redirect_to:1
                        });
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                    this.setState({
                        redirect_to:1
                    });
                });
        };
    }
    componentDidMount() {
        setTimeout(()=>{
            const token=getCookie("token");
            if(token===null || token === ""){
                this.setState({
                    redirect_to:1
                });
            }
            this.setState({
                user_token:token
            });
            const form=new FormData(document.getElementById("login_out_form"));
            const real_data=new URLSearchParams();
            for(const pair of form){
                real_data.append(pair[0],pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/check_lobby_in.php", {
                headers: { "Content-Type": "application/x-www-form-urlencoded" },
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    if(response.status==="empty"){
                        fetch("http://localhost/shareinterest/public_html/get_info.php", {
                            headers: { "Content-Type": "application/x-www-form-urlencoded" },
                            method: 'POST',
                            body: real_data
                        })
                            .then(res => res.json())
                            .then(response => {
                                if(response.username!==null){
                                    this.setState({
                                        user_name:response.username
                                    });
                                }else{
                                    alert('Unknown error occurred, please try again later');
                                }
                            })
                            .catch(err => {
                                alert('Unknown error occurred, please try again later');
                            });
                        fetch("http://localhost/shareinterest/public_html/lobby_list.php", {
                            method: 'POST'
                        })
                            .then(res => res.json())
                            .then(response => {
                                this.setState({
                                    lobbylist:response
                                })
                            })
                            .catch(err => {
                                alert('Unknown error occurred, please try again later');
                            });
                    }else if(response.status!=="error"){
                        this.setState({
                            lobbyid:response.status,
                            lobbyname:response.lobbyname,
                            redirect_to:2
                        });
                        return false;
                    }else{
                        alert('Unknown error occurred, please try again later');
                        this.setState({
                            redirect_to:1
                        });
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                    this.setState({
                        redirect_to:1
                    });
                });
        },100);
    }
    render() {
        if(this.state.redirect_to===1){
            return <Redirect to="/shareinterest/build/" />
        }else if(this.state.redirect_to===2){
            const url=`/shareinterest/build/LobbyView/${this.state.lobbyid}/${this.state.lobbyname}`;
            return <Redirect to={url} />
        }else {
            const welcomehead=`Hi ${this.state.user_name},`,
                welcomepara=`Join lobby to interact with other fans.`;
            return (
                <div>
                    <SideNav welcomehead={welcomehead} welcomepara={welcomepara} />
                    <Main user_token={this.state.user_token} LogmeOut={this.log_me_out} lobbylist={this.state.lobbylist} />
                </div>
            );
        }
    }
}

export default Lobbylist;