import React, {Component} from 'react';
import Login from "./Login";
import Register from "./Register";
import SideNav from "./SideNav";

class Home extends Component {
    RegClick = e => {
       e.preventDefault();
        this.setState({
            visibility: <Login LoginClick={this.LoginClick} />
        });
    };
    LoginClick = e =>{
        e.preventDefault();
        this.setState({
            visibility: <Register RegClick={this.RegClick} />
        });
    };
    state = {
        visibility: <Login LoginClick={this.LoginClick} />
    };
    render() {
        const welcomehead=`Login/Register`,
            welcomepara=`Join lobby and
    Discuss your interest with other fans which have similar interest.`;
        return (
            <div>
                <SideNav welcomehead={welcomehead} welcomepara={welcomepara} />
                <div className="main">
                    <div className="col-md-6 col-sm-12">
                        {this.state.visibility}
                    </div>
                </div>
            </div>
        );
    }
}

export default Home;