import React, {Component} from 'react';
import {Redirect} from 'react-router-dom';

class ListCreater extends Component {
    constructor(props) {
        super(props);
        this.state={
            redrect:0,
        };
    }
    redrect_chat = () =>{
        this.setState({
            redrect:1
        });
    };
    render() {
        if(this.state.redrect){
            const url=`/shareinterest/build/LobbyView/${this.props.lobbyid}/${this.props.lobbyname}`;
            return (
                <Redirect to={url} />
            )
        }else{
            return (
                <tr>
                    <th scope="row">{this.props.lobbyid}</th>
                    <td>{this.props.lobbyname}</td>
                    <td>{this.props.joined}</td>
                    <td>
                        <button onClick={this.redrect_chat} className="btn btn-primary">Join Lobby</button>
                    </td>
                </tr>
            );
        }
    }
}

export default ListCreater;