import React, {Component} from 'react';
import MsgBody from "./Msg_Body";

class ChatBody extends Component {
    constructor(props) {
        super(props);
        this.state = {
            msg_body:"chat-list d-none",
            load_msg:"",
            nomsg_found:"d-none",
            all_msg:""
        };
        this.fetchmesg = () =>{
            if(window.location.href.includes("http://localhost/shareinterest/build/LobbyView")){
                const form=new FormData(document.getElementById("login_out_form"));
                const real_data=new URLSearchParams();
                for(const pair of form){
                    real_data.append(pair[0],pair[1]);
                }
                fetch("http://localhost/shareinterest/public_html/fetchmsg.php", {
                    headers: { "Content-Type": "application/x-www-form-urlencoded" },
                    method: 'POST',
                    body: real_data
                })
                    .then(res => res.json())
                    .then(response => {
                        console.log(response);
                        if(response.status==="empty"){
                            this.setState({
                                nomsg_found:"",
                                load_msg:"d-none"
                            });
                        }else if(response.status==="error"){
                            alert('Unknown error occurred, please try again later');
                        }else{
                            this.setState({
                                nomsg_found:"d-none",
                                load_msg:"d-none",
                                all_msg:response,
                                msg_body:"chat-list"
                            });
                        }
                    })
                    .catch(err => {
                        alert('Unknown error occurred, please try again later');
                    });
                    setTimeout(()=>{
                        this.fetchmesg();
                    },6000);
              }
        };
        setTimeout(()=>{
            this.fetchmesg();
        },2000);
    }
    render() {
        const loading_msg={color:"purple",textAlign:"center",marginTop:"5em"},
        nomsg={color:"red",textAlign:"center",marginTop: "5em"};
        let msg_body="";
        if(this.state.all_msg!==""){
            msg_body=this.state.all_msg.map((msg) => (<MsgBody user_token={this.props.user_token} msg_token={msg.token} username={msg.username} msg={msg.msg} />));
        }
        return (
            <div className="card-body height3" id="chatting_body">
                <div id="loading_message" className={this.state.load_msg}>
                    <h4 style={loading_msg}>Loading Messages ...</h4>
                </div>
                <div className={this.state.nomsg_found}><h4 style={nomsg}>No Message Found ...</h4></div>
                <ul className={this.state.msg_body} id="msg_body">
                    {msg_body}
                </ul>
            </div>

        );
    }
}

export default ChatBody;