import React, {Component} from 'react';
import SideNav from "../App/SideNav";
import getCookie from "../../Module/getCookie";
import ChatLayout from "./Chat_Layout";
import './lobbyview.css'
import {Redirect} from "react-router-dom";

class LobbyView extends Component {
    constructor(props) {
        super(props);
        this.state= {
            redirect_to:0,
            user_token:"",
            user_name:"loading",
            redirectid:"",
            redirect_name:""
        };
    }
    componentDidMount() {
        setTimeout(()=>{
            const token=getCookie("token");
            if(token===null || token === ""){
                this.setState({
                    redirect_to:2
                });
                return false;
            }
            this.setState({
                user_token:token
            });
            setTimeout(()=>{
                const form=new FormData(document.getElementById("login_out_form"));
                const real_data=new URLSearchParams();
                for(const pair of form){
                    real_data.append(pair[0],pair[1]);
                }
                fetch("http://localhost/shareinterest/public_html/get_info.php", {
                    headers: { "Content-Type": "application/x-www-form-urlencoded" },
                    method: 'POST',
                    body: real_data
                })
                    .then(res => res.json())
                    .then(response => {
                        if(response.username!==null){
                            this.setState({
                                user_name:response.username
                            });
                            return false;
                        }else{
                            alert('Unknown error occurred, please try again later');
                        }
                    })
                    .catch(err => {
                        alert('Unknown error occurred, please try again later');
                    });
                fetch("http://localhost/shareinterest/public_html/lobby_update.php", {
                    headers: { "Content-Type": "application/x-www-form-urlencoded" },
                    method: 'POST',
                    body: real_data
                })
                    .then(res => res.json())
                    .then(response => {
                        if(response.status==="redirect"){
                            alert('You have already joined '+response.lobbyname+' Lobby,');
                            this.setState({
                                redirectid:response.lid,
                                redirect_name:response.lobbyname,
                                redirect_to:1
                            });
                            return false;
                        }
                        else if(response.status==="success"){}else{
                            alert('Unknown error occurred, please try again later');
                            this.setState({
                                redirect_to:2
                            });
                        }
                    })
                    .catch(err => {
                        alert('Unknown error occurred, please try again later');
                    });
            },1000);
        },200);
    }

    render() {
        if(this.state.redirect_to===1){
            const url=`/shareinterest/build/LobbyView/${this.state.redirectid}/${this.state.redirect_name}`;
            return <Redirect to={url} />
        }else if(this.state.redirect_to===2){
            return <Redirect to="/shareinterest/build/" />
        }else{
            const welcomehead=`Hi ${this.state.user_name},`,
                welcomepara=`Welcome To ${this.props.match.params.lobbyname} lobby.`,
                lobbyname=`${this.props.match.params.lobbyname} Discussion`;
            return (
                <div>
                    <SideNav welcomehead={welcomehead} welcomepara={welcomepara} />
                    <ChatLayout lobbyname={lobbyname} user_token={this.state.user_token} formlobby_id={this.props.match.params.lobbyid} />
                </div>
            );
        }
    }
}

export default LobbyView;