import React, {Component} from 'react';
import validateEmail from "../../Module/validateEmail";
import validatepass from "../../Module/validatePass";

class Register extends Component {
    constructor(props) {
        super(props);
        this.state = {
            formerror:"",
            logininput:"",
            emailinput:"",
            passinput:"",
            cpassinput:"",
            error_toggle:"d-none text-danger text-capitalize",
        };
        this.logininput = e =>{
            this.setState({
                logininput:e.target.value
            });
        };
        this.emailinput = e =>{
            this.setState({
                emailinput:e.target.value
            });
        };
        this.inputpass = e => {
            this.setState({
                passinput: e.target.value
            });
        };
        this.inputcpass = e => {
            this.setState({
                cpassinput: e.target.value
            });
        };
        this.regerrorfound = text => {
            this.setState({
                formerror: text,
                error_toggle:"text-danger text-capitalize"
            });
            document.getElementById("reg_form_warning").innerText=text;
            document.getElementById("reg_form_warning").classList.remove("d-none");
        };
        this.regnoerrorfound = () => {
            this.setState({
                formerror: "",
                error_toggle:"d-none text-danger text-capitalize"
            });
        };
        this.registerform = e => {
            e.preventDefault();
            const s=this.state;
            if(s.emailinput==="" || s.passinput==="" || s.logininput==="" || s.cpassinput===""){
                this.regerrorfound("Please fill all the details");
                return false;
            }
            if(s.logininput.length<8){
                this.regerrorfound("Username should be at least 8 character long");
                return false;
            }else if(s.logininput.length>20){
                this.regerrorfound("Username cannot be more than 20 character long");
                return false
            }else{
                this.regnoerrorfound();
            }
            if(!validateEmail(s.emailinput)){
                this.regerrorfound("Please check your email address");
                return false;
            }else{
                this.regnoerrorfound();
            }
            if(!validatepass(s.passinput)){
                this.regerrorfound("Password should be min 8 characters long and should contain at least one number and symbol");
                return false;
            }else{
                this.regnoerrorfound();
            }
            if(s.passinput!==s.cpassinput){
                this.regerrorfound("Password and Confirm password not same");
                return false;
            }else{
                this.regnoerrorfound();
            }
            const form=new FormData(e.target);
            const real_data=new URLSearchParams();
            for(const pair of form){
                real_data.append(pair[0],pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/reg_.php", {
                headers: { "Content-Type": "application/x-www-form-urlencoded" },
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    console.log(response);
                    if(response.status==="success"){
                        if(response.token!==undefined){
                            document.cookie = "token="+response.token+"; path=/; max-age=" + 7*24*60*60;
                            window.location.reload();
                        }else{
                            alert('Unknown error occurred, please try again later');
                        }
                    }else if(response.status==="user_already"){
                        this.regerrorfound("Username with this name already exist.");
                        return false;
                    }else if(response.status==="email_already"){
                        this.regerrorfound("This email is already registered, please click to login button to login");
                    }else if(response.status==="pass_invalid"){
                        this.regerrorfound("Password is very weak");
                    }else if(response.status==="pass_not_same"){
                        this.regerrorfound("Password and confirm password is not same");
                    }else {
                        alert('Unknown error occurred');
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                });

        };
    }
    render() {
        return (
            <div className="login-form" id="registration_form">
                <form id="reg_submit_form" onSubmit={this.registerform}>
                    <div className="form-group">
                        <label>Username</label>
                        <input type="text" name="username" onChange={this.logininput} value={this.state.logininput} id="username_id" required className="form-control" placeholder="Enter Username"/>
                    </div>
                    <div className="form-group">
                        <label>E-mail</label>
                        <input type="email" name="email" onChange={this.emailinput} value={this.state.emailinput}  id="email_id" required className="form-control" placeholder="Enter E-mail"/>
                    </div>
                    <div className="form-group">
                        <label>Password</label>
                        <input onChange={this.inputpass} value={this.state.passinput} type="password" name="password" maxLength="16" id="pass_id" required className="form-control" placeholder="Password"/>
                    </div>
                    <div className="form-group">
                        <label>Confirm Password</label>
                        <input onChange={this.inputcpass} value={this.state.cpassinput} type="password" name="c_password" maxLength="16" id="c_pass_id" required className="form-control" placeholder="Password"/>
                    </div>
                    <div className={this.state.error_toggle} id="reg_form_warning">{this.state.formerror}</div>
                    <button type="submit" id="new_register" className="btn btn-black">Register</button>
                    <button onClick={this.props.RegClick} type="button" id="redirect_to_login" className="btn btn-secondary left1em">Already Registered! Login
                    </button>
                </form>
            </div>
        );
    }
}

export default Register;