import React, {Component} from 'react';
import validateEmail from "../../Module/validateEmail";
import validatePass from "../../Module/validatePass";
import {Redirect} from 'react-router-dom';

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            formerror:"",
            logininput:"",
            passinput:"",
            error_toggle:"d-none text-danger text-capitalize",
            redirecttolist:1
        };
        this.submit_login = e => {
            e.preventDefault();
            const s = this.state;
            if (s.logininput === "" || s.passinput === "") {
                this.lregerrorfound("Please fill all the details");
                return false;
            } else {
                this.lregnoerrorfound();
            }
            if (!validateEmail(s.logininput) || !validatePass(s.passinput)) {
                this.lregerrorfound("Please check your credentials");
                return false;
            } else {
                this.lregnoerrorfound();
            }
            const form = new FormData(e.target);
            const real_data = new URLSearchParams();
            for (const pair of form) {
                real_data.append(pair[0], pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/login_.php", {
                headers: {"Content-Type": "application/x-www-form-urlencoded"},
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    if (response.status === "success") {
                        if (response.token !== undefined) {
                            document.cookie = "token=" + response.token + "; path=/; max-age=" + 7 * 24 * 60 * 60;
                            this.setState({
                                redirecttolist: 0
                            });
                        } else {
                            alert('Unknown error occurred, please try again later');
                        }
                    } else if (response.status === "invalid") {
                        this.lregerrorfound("Please check your credentials");
                        return false;
                    } else {
                        alert('Unknown error occurred');
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                });
        };
        this.lregerrorfound = text => {
            this.setState({
                formerror: text,
                error_toggle:"text-danger text-capitalize"
            });
        };
        this.lregnoerrorfound = () => {
            this.setState({
                formerror: "",
                error_toggle:"d-none text-danger text-capitalize"
            });
        };
        this.inputemail = e =>{
            this.setState({
                logininput:e.target.value
            });
        };
        this.inputpass = e => {
            this.setState({
                passinput: e.target.value
            });
        }
    }
    render() {
        if(this.state.redirecttolist){
            return (
                <div className="login-form" id="login_form">
                    <form onSubmit={this.submit_login} id="login_submit_form">
                        <div className="form-group">
                            <label>E-mail</label>
                            <input value={this.state.logininput} onChange={this.inputemail} type="email" id="login_email_id" name="email" required className="form-control" placeholder="Enter E-mail"/>
                        </div>
                        <div className="form-group">
                            <label>Password</label>
                            <input value={this.state.passinput} onChange={this.inputpass} type="password" id="login_pass_id" name="password" required className="form-control" placeholder="Password"/>
                        </div>
                        <div className={this.state.error_toggle} id="login_form_warning">{this.state.formerror}</div>
                        <button type="submit" id="new_login" className="btn btn-black">Login</button>
                        <button onClick={this.props.LoginClick} type="button" id="redirect_to_reg" className="btn btn-secondary left1em">Create Account
                        </button>
                    </form>
                </div>
            );
        }else{
            return <Redirect to="/shareinterest/build/lobbylist" />
        }
    }
}

export default Login;