import React, {Component} from 'react';
import {Redirect} from "react-router-dom";
import ChatBody from "./Chat_Body";

class ChatLayout extends Component {
    constructor(props) {
        super(props);
        this.state={
            chat_msg:"",
            redirect_to:0
        };
        this.update_chat_msg = e => {
            this.setState({
                chat_msg:e.target.value
            });
        };
        this.log_me_out = () => {
            const form=new FormData(document.getElementById("login_out_form"));
            const real_data=new URLSearchParams();
            for(const pair of form){
                real_data.append(pair[0],pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/log_out.php", {
                headers: { "Content-Type": "application/x-www-form-urlencoded" },
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    if(response.status==="success"){
                        document.cookie = "token= ; path=/; max-age=" + 0;
                        this.setState({
                            redirect_to:1
                        });
                        return false;
                    }else{
                        alert('Unknown error occurred, please try again later');
                        this.setState({
                            redirect_to:1
                        });
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                    this.setState({
                        redirect_to:1
                    });
                });
        };
        this.leave_me_out = () =>{
            const form=new FormData(document.getElementById("login_out_form"));
            const real_data=new URLSearchParams();
            for(const pair of form){
                real_data.append(pair[0],pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/leave_lobby.php", {
                headers: { "Content-Type": "application/x-www-form-urlencoded" },
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    if(response.status==="success"){
                        this.setState({
                            redirect_to:2
                        });
                        return false;
                    }else{
                        alert('Unknown error occurred, please try again later');
                        this.setState({
                            redirect_to:1
                        });
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                    this.setState({
                        redirect_to:1
                    });
                });
        };
        this.MsgSender_Serve= () =>{
            if(this.state.chat_msg.length<1){
                alert("You Cannot Send Empty Messages");
                return false;
            }
            const form=new FormData(document.getElementById("login_out_form"));
            const real_data=new URLSearchParams();
            for(const pair of form){
                real_data.append(pair[0],pair[1]);
            }
            fetch("http://localhost/shareinterest/public_html/msg_sender.php", {
                headers: { "Content-Type": "application/x-www-form-urlencoded" },
                method: 'POST',
                body: real_data
            })
                .then(res => res.json())
                .then(response => {
                    if(response.status==="success"){
                        this.setState({
                            chat_msg:""
                        });
                    }else if(response.status==="error"){
                        alert('Unknown error occurred, please try again later');
                    }
                })
                .catch(err => {
                    alert('Unknown error occurred, please try again later');
                    this.setState({
                        redirect_to:1
                    });
                });
        };
    }
    render() {
        if(this.state.redirect_to===1){
            return <Redirect to="/shareinterest/build/" />
        }else if(this.state.redirect_to===2){
            return <Redirect to="/shareinterest/build/lobbylist" />
        }else{
            const tpop_pad={padding:"1em",marginTop:"2em"};
            return (
                <div className="main">
                    <div className="col-md-10 col-sm-12" style={tpop_pad}>
                        <button className="btn btn-danger float-right" id="log_me_out" onClick={this.log_me_out}>Log Out</button>
                        <form method="post" className="d-none" id="login_out_form">
                            <input type="text" id="user_token" value={this.props.user_token} name="token"/>
                            <input type="hidden" id="formlobby_id" value={this.props.formlobby_id} name="lobby_id"/>
                            <input type="text" id="send_msg_form" value={this.state.chat_msg} name="message"/>
                        </form>
                        <button id="actionleave_lobby" className="btn btn-danger" onClick={this.leave_me_out}>Leave Lobby</button>
                        <br/><br/><br/><br/>
                        <div className="card">
                            <div className="card-header" id="chat_header">{this.props.lobbyname}</div>
                            <ChatBody token={this.props.user_token} />
                        </div>
                        <div className="input-group mb-3">
                            <input id="msg_text" value={this.state.chat_msg} onChange={this.update_chat_msg} className="form-control" type="text" placeholder="Enter messages here..."/>
                            <div className="input-group-append">
                                <span id="send_msg" className="input-group-text" onClick={this.MsgSender_Serve}>Send</span>
                            </div>
                        </div>
                    </div>
                </div>
            );
        }
    }
}

export default ChatLayout;